// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import './assets/style/reset.css'
import './assets/style/border.css'
import './assets/icon/iconfont.css'
import 'swiper/dist/css/swiper.css'
import './assets/javascript/reset.js'
import { Row,Col,Field,Button,Tabs,Tab,Switch,Steps,Step,CellGroup,Cell,NavBar,Card,Stepper,Checkbox,Popup } from 'vant'
import VueAwesomeSwiper from 'vue-awesome-swiper'
import axios from 'axios'

Vue.config.productionTip = false
Vue.use(Row).use(Col).use(Field).use(Button).use(Tabs).use(Tab).use(Switch).use(Steps).use(Step).use(CellGroup).use(Cell).use(NavBar).use(Card).use(Stepper).use(Checkbox).use(Popup)
Vue.use(VueAwesomeSwiper)
Vue.prototype.$axios = axios

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



