<template>
    <div class="addr-modify">
        <van-nav-bar
            :title="`${title}收货地址`"
            right-text="保存"
            left-arrow
            @click-left="back"
            @click-right="save"
        ></van-nav-bar>
        <van-cell-group>
            <van-cell>
                <template slot="title">
                    <input class="left-prompt" type="text" v-model="contacts" placeholder="收货人"/>
                </template>
                <div slot="right-icon">
                    <span class="iconfont icon-contacts">&#xe6e7;</span>
                </div>
            </van-cell>
            <van-cell>
                <template slot="title">
                    <input class="left-prompt" type="number" v-model="telephone" placeholder="手机号码"/>
                </template>
                <div slot="right-icon">
                    <span class="telephone">+86<span class="iconfont icon-tel">&#xe667;</span></span>
                </div>
            </van-cell>
            <van-cell>
                <template slot="title">
                    <input class="left-prompt" type="text" v-model="region" placeholder="所在地区"/>
                </template>
                <div slot="right-icon">
                    <span class="telephone"><span class="iconfont icon-tel">&#xe667;</span></span>
                </div>
            </van-cell>
            <van-cell>
                <template slot="title">
                    <textarea class="left-prompt left-area" type="text" v-model="address" placeholder="详细地址：如道路、门牌号、小区、楼栋号、单元室等"/>
                </template>
            </van-cell>
            <div class="addr-wrap">
                <van-cell title="设为默认地址">
                    <template slot="right-icon">
                        <van-switch v-model="checked" />
                    </template>
                </van-cell>
                <van-cell v-if="delFlag">
                    <template slot="title">
                        <button class="left-prompt left-btn" @click="del">删除收货地址</button>
                    </template>
                </van-cell>
            </div>
        </van-cell-group>
    </div>    
</template>

<script>
import { mapState,mapMutations } from 'vuex'
export default {
    name: 'AddrModify',
    data() {
        return {
            contacts: '',
            telephone: '',
            region: '',
            address: '',
            checked: false,
            index: 1
        }
    },
    computed: {
        ...mapState(['addressArr','addrIndex']),
        type() {
            return this.$route.params.type;
        },
        title() {
            return this.type == 'edit' ? '编辑' : '添加';
        },
        delFlag() {
            if(this.type == 'edit') {
                this.setVal();
                return true;
            }else {
                this.setDefault({});
                return false;
            }
        }
    },
    methods: {
        ...mapMutations(['addAddr','editAddr','delAddr']),
        back() {
            this.$router.replace('/address_manage');
        },
        save() {
            let targetObj = {
                contacts: this.contacts,
                telephone: this.telephone,
                region: this.region,
                address: this.address,
                checked: this.checked
            }
            if(this.delFlag) {
                targetObj.id = this.addrIndex;
                this.editAddr(targetObj);
            }else {
                targetObj.id = this.index++;
                this.addAddr(targetObj);
            }
            this.back();
        },
        setVal() {
            let arr = this.addressArr.filter((item) => item.id == this.addrIndex);
            this.setDefault(arr[0]);
        },
        setDefault(obj) {
            this.contacts = obj.contacts || '';
            this.telephone = obj.telephone || '';
            this.region = obj.region || '';
            this.address = obj.address || '';
            this.checked = obj.checked || false;
        },
        del() {
            this.delAddr(this.addrIndex);
            this.back();
        }
    }
}
</script>

<style lang="stylus" scoped>
    @import '~stylus/mixin'
    .addr-modify
        full_screen(2,fixed,0,0,0,0,$lightGray)
        van_nav(#FF9900)
        .addr-wrap
            margin-top .2rem
        & >>> .van-cell-group 
            background-color transparent
            .van-cell
                text-align left
                &:not(:last-child)::after
                    left 0
                .left-prompt
                    placeholder(.28rem)
                    width 90%
                    height 100%
                    &.left-area
                    &.left-btn
                        border_none()
                    &.left-btn
                        color $red
                        width 100%
                        text-align left
                        padding 0
                        background-color transparent
                .icon-contacts
                    font-weight bold
                    font-size .5rem
                .telephone
                    color $darkGray
                    font-size .3rem
                    margin-right -.1rem
                    .icon-tel
                        font-size .28rem
        & >>> .van-switch
            font-size .4rem!important
</style>



